import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import MiniBox from '../../../layout/MiniBox';
import Icon from '../../../components/Icon';
import style from './DepositCheckStart.module.css';
import { useAppAsyncDispatch } from '../../../hooks/store';
import { updateCheckDepositData } from '../../../store/actionCreators/checkpayment';
import { useDrawer } from '../../../hooks/drawer';

const Status = React.lazy(() => import('../DepositCheckStatus/DepositCheckStatus'));
const Form = React.lazy(() => import('../DepositCheckRequirements/DepositCheckRequirements'));

/**
 * Check deposit page
 * @returns {React.ReactNode}
 */
const DepositCheckStart = () => {
  const { t } = useTranslation();
  const { close, open } = useDrawer();

  const dispatch = useAppAsyncDispatch();

  const startDeposit = useCallback(() => {
    // reset then form data
    dispatch(updateCheckDepositData({
      frontImage: null,
      amount: '',
      backImage: null
    }));
    setTimeout(() => {
      open(Form);
    }, 0);
  }, []);

  return (
    <div className="page">
      <MiniBox
        heading={t('deposit_check_start.title')}
        showBack
        onBack={() => {
          close();
        }}
      >
        <Box width="100%" paddingTop="15px">
          <HStack
            className={style.transferType}
            border="1px solid var(--border-color-1)"
            onClick={startDeposit}
          >
            <Box>
              <Text variant="regular">{t('deposit_check_start.depositCheck')}</Text>
            </Box>
            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
          </HStack>
          <HStack
            className={style.transferType}
            border="1px solid var(--border-color-1)"
            onClick={() => {
              open(Status);
            }}
          >
            <Box>
              <Text variant="regular">{t('deposit_check_start.checkStatus')}</Text>
            </Box>
            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
          </HStack>

        </Box>
      </MiniBox>

    </div>
  );
};

export default DepositCheckStart;
