/* eslint-disable import/prefer-default-export */
import { actionCreator } from '..';
import { checkDepositData, check } from '../../types/checkdeposit';
import { UPDATE_CHECK_DEPOSIT_DATA, UPDATE_CHECK_DEPOSIT_LIST } from '../actionTypes/checkpayment';

/**
 * update chek deposit payload
 * @param {*} payload
 * @returns {Action<string, any>}
 */
export const updateCheckDepositData: actionCreator<checkDepositData> = (payload) => ({
  type: UPDATE_CHECK_DEPOSIT_DATA,
  payload
});

/**
 * update chek deposit list
 * @param {*} payload
 * @returns {Action<string, any>}
 */
export const updateCheckList: actionCreator<{
  total: number
  data: check[]
}> = (payload) => ({
  type: UPDATE_CHECK_DEPOSIT_LIST,
  payload
});
